#content{
  width: calc(100% - 312px - 16px);
  height: calc(100vh - 48px - 16px);
  overflow: auto;
  margin: 8px;
  position: absolute;
  top: 0px;
  right: 0px;
}

#postFAB{
  z-index: 1;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 140px;
  right: 20px;
  width: 40px;
  height: 40px;
  background: #b4ffb4;
  border-radius: 50%;
  box-shadow: 5px 5px 5px 0 grey;
}
#postFAB svg{
  color: #606060;
  font-size: 30px;
}

#scrollFAB{
  z-index: 1;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 90px;
  right: 20px;
  width: 40px;
  height: 40px;
  background: #b4ffb4;
  border-radius: 50%;
  box-shadow: 5px 5px 5px 0 grey;
}
#scrollFAB svg{
  color: #606060;
  font-size: 30px;
}

#menuFAB{
  z-index: 1;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 40px;
  right: 20px;
  width: 40px;
  height: 40px;
  background: #b4ffb4;
  border-radius: 50%;
  box-shadow: 5px 5px 5px 0 grey;
}
#loading{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  background: white;
}

#fakeTextarea {
	min-height: 0;
	height: 0px;
	border: 0;
	width: 100%;
	opacity: 0;

  font-size: 0.9rem;
  position: absolute;
  top: -10000000px;
}

@media(max-width: 500px){
    #guideWrapper{
      width: 230px;
    }
    #content{
      width: calc(100% - 16px);
    }
  }